<template>
  <div class="container">
    <div class="main__search">
        <input type="text"
         class="main__search-input"
         :placeholder="placeholder"
         v-model="value"
         autofocus
         >
         <div class="main__content-list" v-if="getSearchData">
            <SearchContent 
            v-for="item in getSearchData" 
            :key="item.id"
            :item="item"
            
            />
         </div>

    </div>

  </div>
</template>

<script>
import SearchContent from '@/components/Search/SearchContent.vue';
import { mapState } from 'vuex';


export default {
    props: ["placeholder"],
    data() {
        return {
            value: ""
        };
    },
    computed: {
        ...mapState('search', ['getSearchData']),
    },
    watch: {
        value(newVal) {
                this.$store.dispatch("search/getSearchData", this.value);

        }
    },
    components: { SearchContent }
}
</script>

